body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.k-card {
  margin: 50px;
  margin-top: 20px !important;
}

.k-card-image {
  border-radius: 10px;
  width: 300px;
  height: 200px;
  filter: drop-shadow(4px 4px 20px #ccc);
  transition: transform 0.2s;
}

.k-card-image:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.k-card-title {
  font-size: 24px;
  margin: 0px;
}

.k-card-info {
  margin-left: 75px;
  min-height: 100px;
}

k-card-sub-title {
  font-size: 16px;
  margin: 0px;
}

.k-avatar {
  border-radius: 100%;
  object-fit: contain;
  width: 350px;
  height: auto;
}

.imageShadow {
  box-shadow: 0px 0px 3px 1px #ccc;
}

.textShadow {
  text-shadow: 4px 5px 5px #ccc;
}

.titleHome {
  margin-top: 20px;
}

.success-msg {
  color: #57a773;
  padding: 10px 15px;
}

.error-msg {
  color: #ee6352;
  padding: 10px 15px;
}
